* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background: url("https://i.ibb.co/h8RZwhY/base.jpg") no-repeat center fixed;
  background-size: cover;
  font-family: 'Poppins', sans-serif;
}

.btn {
  display: inline-block;
  background: #000;
  color: #fff;
  border: none;
  padding: 10px 20px;
  margin: 5px;
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
  font-size: 15px;
  font-family: inherit;
}

.btn:focus {
  outline: none;
}

.btn:active {
  -webkit-transform: scale(0.98);
          transform: scale(0.98);
}

.btn-block {
  display: block;
  width: 100%;
}

